.FlexibleInfoBox {
    width: 416px;

    .FlexibleInfoBox-image-wrapper {
        display: block;
        margin: 28px 16px 14px 16px;       

        .FlexibleInfoBox-compile-image {
            background-image: url('../../assets/images/compile.png');
            background-repeat: no-repeat;
            background-size: 367px 84px;     
            width: 367px;
            height: 84px;       
        }

        .FlexibleInfoBox-deploy-image {
            background-image: url('../../assets/images/deploy.png');
            background-repeat: no-repeat;
            background-size: 367px 84px;     
            width: 367px;
            height: 84px;            
        }

        .FlexibleInfoBox-run-image {
            background-image: url('../../assets/images/run.png');
            background-repeat: no-repeat;
            background-size: 367px 84px;     
            width: 367px;
            height: 84px;            
        }
    }        
    
    .FlexibleInfoBox-message-box {
        display: block;
        position: absolute;
        top: 28px;
        right: -368px;
        width: 368px;
        height: 88px;
        background-color: #1d2633;

      

        .FlexibleInfoBox-message-box-title {
            width: 318px;
            .FlexibleInfoBox-message-box-title-ic {
                display: inline-block;
                background-image: url('../../assets/icon/ic-success.png');
                background-repeat: no-repeat;
                background-size: cover;
                width: 14px;
                height: 14px;                
                margin: 14px 10px 0px 24px;
            }

            .FlexibleInfoBox-message-box-title-text {
                display: inline-block;
                font-size: 18px;
            }
        }

        .FlexibleInfoBox-message-box-body {
            display: flex;
            justify-content: space-between;
            .FlexibleInfoBox-message-box-body-text {
                width: 318px;
                display: inline-block;
                opacity: 0.6;
                font-size: 13px;
                margin: 7px 10px 0px 24px;
            }

            .FlexibleInfoBox-message-box-body-close-btn {
                background-image: url('../../assets/icon/ic-close.png');
                background-size: 14px 14px;
                display: inline-block;
                width: 14px; 
                height: 14px;
                content:"";
                margin-right: 15px;
                
                &:hover {
                    cursor: pointer;
                }
            }
        }     
    } 
    
    @keyframes show-execution-result {
        0%   {right:-368px;}
        100% {right:32px;}
    }
    
    @keyframes hide-execution-result {
        0%   {right:32px;}
        100% {right:-368px;}        
    }

    .FlexibleInfoBox-message-box-show {
        animation-name: show-execution-result;
        animation-duration: 1s; 
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;            
    }

    .FlexibleInfoBox-message-box-hide {
        animation-name: hide-execution-result;
        animation-duration: 1s; 
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;            
    }     
}